import { Component, Host, Prop, h } from '@stencil/core';

@Component({
  tag: 'app-container',
  styleUrl: 'app-container.css',
  shadow: false,
})
export class AppContainer {
  @Prop() value: string;
  @Prop() height: string;
  @Prop() width: string;
  @Prop() x: string;
  @Prop() y: string;
  @Prop() z: string;
  @Prop() bgColor: string;
  @Prop() type: string;
  @Prop() visible: boolean;
  @Prop() audio: string;
  @Prop() onTouch: string;
  @Prop() onMatch: string;
  @Prop() onEntry: string;

  render() {
    const style = {
      height: this.height,
      width: this.width,
      backgroundColor: this.bgColor,
      top: this.y,
      left: this.x,
    };

    return (
      <Host class="container" style={style}>
        {/* <slot /> */}
      </Host>
    );
  }
}
